import React from 'react';
import './GenForm.css';

export class GenForm extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            val: 'Enter your information',
            name: '',
            email: '',
            message: ''
        };
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.onEmailChange = this.onEmailChange.bind(this);
        this.onNameChange = this.onNameChange.bind(this);
        this.onMessageChange = this.onMessageChange.bind(this);
    }

    handleChange(e) {
        this.setState({
            val: e.target.value
        });
    }

    onNameChange(e) {
        this.setState({
            name: e.target.value
        });
      }
    
      onEmailChange(e) {
        this.setState({
            email: e.target.value
        });
      }
    
      onMessageChange(e) {
        this.setState({
            message: e.target.value
        });
      }

    handleSubmit(e) {
        alert('Information was submitted: ' + this.state.name + ' has an email of ' + this.state.email + ' with message: ' + this.state.message);
        e.preventDefault();
    }

    render() {
        console.log("XXX" + this.state.name);
        return <form id="contact-form" onSubmit={this.handleSubmit} method="POST">
        <div className="form-group">
            <label htmlFor="name">Name</label>
            <input type="text" onChange={this.onNameChange} className="form-control" />
        </div>
        <div className="form-group">
            <label htmlFor="exampleInputEmail1">Email address</label>
            <input type="email" onChange={this.onEmailChange} className="form-control" aria-describedby="emailHelp" />
        </div>
        <div className="form-group">
            <label htmlFor="message">Message</label>
            <textarea onChange={this.onMessageChange} className="form-control" rows="5"></textarea>
        </div>
        <button type="submit" className="btn btn-primary">Submit</button>
    </form>;
    }
}